.checkbox1 {
  position: relative;
  padding-right: 15px;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.checkbox1 input[type=checkbox] {
  position: absolute;
  height: inherit;
  width: inherit;
  opacity: 0;
  left: 0;
}
.checkbox1 span {
  cursor: pointer;
  position: relative;
  margin-right: 5px;
  display: inline-block;
  height: 20px;
  width: 20px;
  vertical-align: middle;
}
.checkbox1 span:before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: " ";
  color: #fff;
  text-align: center;
  font-size: 22px;
  border: solid 2px #ddd;
  line-height: 16px;
}
.checkbox1 input:checked + span:before {
  background: #06c5ac;
  border-color: #06c5ac;
  content: "\2713";
}
.radio1 {
  position: relative;
  padding-right: 15px;
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}
.radio1 input[type=radio] {
  position: absolute;
  height: inherit;
  width: inherit;
  opacity: 0;
  left: 0;
}
.radio1 span {
  cursor: pointer;
  position: relative;
  margin-right: 5px;
  display: inline-block;
  height: 20px;
  width: 20px;
  vertical-align: middle;
}
.radio1 span:before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: " ";
  color: #fff;
  text-align: center;
  font-size: 22px;
  border: solid 2px #ddd;
  line-height: 16px;
  border-radius: 30px;
